import { Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs/Observable';

import { GroupsService } from '../groups.service';
import { Group, Person } from '../groups.model';

@Component({
  selector: 'app-group-detail',
  template: require('./group-detail.component.html'),
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GroupDetailComponent implements OnInit {
  private group: Group;
  private groupName = new FormControl();

  constructor(private route: ActivatedRoute,
    private groupsService: GroupsService,
    private changeDetector: ChangeDetectorRef) {
  }

  ngOnInit() {
    // gets the id the the url/route
    this.route.params.map(params => params['id']).switchMap<Group>(id => {
      return this.groupsService.get(id);
    }).subscribe(group => {
      this.group = group;
      this.groupName.setValue(group.name);
      this.changeDetector.markForCheck();
    });
  }

  setGroupName(name: string) {
    this.groupsService.patch(this.group._id, { 'name': name });
  }

  addPerson(person: Person) {
    this.groupsService.addMember(this.group, person);
  }
}



